<template>
   <!-- v-layout row wrap justify-center align-center >
      <v-flex class='ds-img' xs3 v-for='i in quality' style='padding: 1px' >
         <v-img cover :src='i' aspect-ratio='1.5' max-height=260 > </v-img>
      </v-flex>
   </v-layout> -->

   <div style='height: calc(100vh - 48px); position: relative; z-index: 1000 !important;'>

       <vue-easy-lightbox
         moveDisabled
         :visible="visible"
         :imgs="imgs"
         :index='index'
         @hide='handleHide'
         style='z-index: 10000000 !important;position: fixed; top: 400vh; height: 100vh; width: 100vw'
               
         ></vue-easy-lightbox>
      <div class='gridWrapper' style='height: 100%;  ' >
         <div v-for='(i,k) in q2' :id="'q'+(k+1)" style='background: white;' 
            class='innerGrid'
            
         >
            <v-img ::lzy-src='i' :cover='k!=2' :contain='k==2'
               :src='i+"?lazy"'
               
               :aspect-ratio='k==2? 1.9 : 0'
                           
               position='center center'
               @click='handleExpand(k)'
            > 
            </v-img>

            
         </div>
      </div>
   </div>
</template>

<script>

import d from '../../assets/about.js' 

export default {

   data(){
      return {
         q2: d.q2,
         
         imgs: d.q2,
         index: 0,
         visible: false
      }
   },
   methods: {
      handleExpand(i){

   

         this.index=i
         this.visible = true
         //alert(this.$parent.visible)
         
      },
      handleHide(){
         this.visible = false
      }
   },
   

}
</script>

<style>

   #q6{
      grid-column: 2/4;
   }
   #q7{
      grid-row: 2/4;
      grid-column: 4;
   }

</style>
