<template>
   <v-layout fill-height column justify-center  >
      <!-- <gallery class='image' :images="data" :index="index" @close="index = null"></gallery> -->
      <v-flex xs12  class=' '>

         <full-page ref='fullpage' :options='options' id='fullpage menu'>
            <div class='section' >

               <infra> </infra> 

               <!-- Infrastructure -->

            </div>

            <div class='section'>
               <quality> </quality>
            </div>

            <div class='section'>
               <lab> </lab>
            </div>

            <div class='section'>
               <design> </design>
            </div>

            <div class='section'>
               <q2> </q2>
            </div>

            <div class='section'>
               <ap> </ap>
            </div>

         
         </full-page>
   
      </v-flex>
   </v-layout>
</template>

<script>

import infra from './grid/infra'
import lab from './grid/lab'
import design from './grid/design'
import quality from './grid/quality'
import ap from './grid/ap'
import q2 from './grid/q2'
import cc from './grid/cc'

import cdata from '../assets/certifications/new'

import Vue from 'vue'





export default {



   components: {infra,quality,design,lab,cc,ap,q2},
   data(){
      return {

         imgs: '',
         visible: false,

         options: {
            licenseKey: 'ABF5a675$',
            menu: '#fullpage',
            anchors: ['page1', 'page2', 'page3','page4','page5','page6','page7'],
            paddingTop: '0px',
            paddingBottom: '36px',
            css3: true,
            
            onLeave: (origin, destination, direction) => {

               //console.log( destination.item.childNodes[0].childNodes[0].childNodes[1])
               this.$anime({
                  targets: destination.item.childNodes[0].childNodes[0].childNodes[1].childNodes,
                  translateY: direction == 'down' ? ['50%','0%'] : ['-50%','0%'],
                  opacity: [0,1],
                  
                  delay: this.$anime.stagger(100,{from:'first'}),
                  easing: 'easeInOutQuad',
                  duration: 500
               })
            }
         
            
         },

         temp: [],

         cIndex: null,
         index: null,
         data: [
           
         ]
      }
   },
   methods: {
      doAnimation: function(){
         this.$anime({
            targets: '.ds-img',
            opacity: [0,1],
            translateY: ['10',0],
            delay: (el,i) => {
               return i*50
            },
            duration: 1000,
            easing: 'linear'

      })
      }
   },

   beforeRouteEnter(to,from,next)
   {
      //document.getElementById('ftr').classList.add('ftr')
      next()
   },

   beforeRouteLeave(to,from,next){

     // alert('leaving')
      //document.getElementById('ftr').classList.remove('ftr')

      this.$nextTick( ()=> {
         next()
      } )
      
   },

   mounted() {
         console.log(cdata)
   },

   computed: {
      certificates(){
         return cdata
      }
   }

}
</script>

<style>


   #style-1::-webkit-scrollbar-track
   {
   
   
      border-radius: 10px;
      background-color: #F5F5F5;
      margin: 20% 0 50%;
   


   }

   #style-1::-webkit-scrollbar
   {
      width: 18px;
      
     

      
   }

   #style-1::-webkit-scrollbar-thumb
   {
     
      border-radius: 10px;
      background-color: rgba(0,0,0,.2);
      
   }

    .innerGrid{
      overflow: hidden;  /* NEW */
      min-width: 0;      /* NEW; needed for Firefox */
   }


   .cImg  {
     border: 1px solid rgba(0,0,0,.3);
     margin: 5px;
   }

   .ccImg:nth-child(odd){
      transform:  translate(25%) rotate(-15deg);
      box-shadow: -5px 15px 0px 5px rgba(0,0,0,.3)
      
   }
   .ccImg:nth-child(even){
      transform:  translate(-10%) rotate(15deg);
      box-shadow: 5px 15px 0px 5px rgba(0,0,0,.3)
   }


   .ccImg:nth-child(1n){
      margin-bottom: -30%
   }

   .certi {
      opacity: .5;
      transition: all linear .2s;
      cursor: pointer;
   }
   .certi:hover{
      opacity: 1
   }

   .iimg{
      transition: all linear .2s;
   }

   .iimg:hover{

      cursor: pointer;
      transform: scale(1.2);
      

   }

   .iimg:hover:after{
      content: '';
      background: rgba(0,0,0,.1);
      width: 100%;
      height: 100%;

   }

   .section {

      max-height: calc(100vh) !important;
      overflow: hidden !important;
      min-width: 0;  
      position: relative;
   
      
   }

   .hdr{
      z-index: 55;
      top: 0px;
   }

   #ftr {
      z-index: 55;
      bottom: 0px;
   }
   .fade-enter-active, .fade-leave-active {
   transition: all .5s;
   opacity: 1;
   }
   .fade-enter, .fade-leave-active {
   opacity: 0;
   }

     .gridWrap{
              display: grid;
              grid-template-columns: repeat(4,1fr);
              grid-gap: 0px !important;
              grid-auto-rows: auto;
              
       }

       .gridWrap > div{
              background: #ddd;
              padding: 1em !important;
       }

       .gridWrap > div:nth-child(odd){
              background: #eee;
       }

      .gridImgStretch{
         width: 100% !important;
         height: 100% !important;
      }



</style>
