<template>
   <!-- v-layout row wrap justify-center align-center >
      <v-flex class='ds-img' xs3 v-for='i in quality' style='padding: 1px' >
         <v-img cover :src='i' aspect-ratio='1.5' max-height=260 > </v-img>
      </v-flex>
   </v-layout> -->

  <div style='height: calc(100vh - 48px); position: relative; z-index: 1000 !important;'>
      
       <vue-easy-lightbox
         moveDisabled
         :visible="visible"
         :imgs="imgs"
         :index='index'
         @hide='handleHide'
         style='z-index: 10000000 !important;position: fixed; top: 500vh; height: 100vh; width: 100vw'
               
         ></vue-easy-lightbox>

      <div class='gridWrapper' style='height: 100%;  ' >
         <div v-for='(i,k) in ap' :id="'ap'+(k+1)" style='background: white;' 
            class='innerGrid'

         >
            <v-img ::lzy-src='i' 
                   :src='i+"?lazy"'
                  :cover='k!=0 || k!=6' 
                  :contain='k==0 || k==6'

               
               :aspect-ratio='k==0 || k == 6  ? 1.7 : 0'
               
               @click='handleExpand(k)'
               > 
            </v-img>

            
         </div>
      </div>
   </div>


</template>

<script>

import d from '../../assets/about.js' 

export default {

   data(){
      return {
         ap: d.ap,
         
         imgs: d.ap,
         index: 0,
         visible: false

      }
   },

   methods: {
      handleExpand(i){

   

         this.index=i
         this.visible = true
         //alert(this.$parent.visible)
         
      },
      handleHide(){
         this.visible = false
      }
   },

}
</script>

<style>


   #ap1{
         grid-row: 1;
         grid-column: 1;
         background: red;
   }


   #ap3{
      grid-column: 4 !important;
      grid-row: 1 !important;
   }

 

</style>
