<template>
   <!-- v-layout row wrap justify-center align-center >
      <v-flex class='ds-img' xs3 v-for='i in quality' style='padding: 1px' >
         <v-img cover :src='i' aspect-ratio='1.5' max-height=260 > </v-img>
      </v-flex>
   </v-layout> -->

   <div style='height: calc(100vh - 48px); position: relative; z-index: 1000 !important;'>
      
      <vue-easy-lightbox
         moveDisabled
         :visible="visible"
         :imgs="imgs"
         :index='index'
         @hide='handleHide'
         style='z-index: 10000000 !important;position: fixed; top: 300vh; height: 100vh; width: 100vw'
               
      ></vue-easy-lightbox>
      
      <div class='gridWrapper' style='height: 100%;' >
         <div v-for='(i,k) in design' :id="'dd'+(k+1)" style='background: white;' 
            class='innerGrid'
            
         >
            <v-img ::lzy-src='i' 
               :src='i+"?lazy"'
               
               :cover='k!=1'
               :contain='k==1'
               
               :aspect-ratio='k==1 ? 1.7 : 0'

               position='center center'
               
               @click='handleExpand(k)'
            
               
            > 
            </v-img>

            
         </div>
      </div>
   </div>
</template>

<script>

import d from '../../assets/about.js' 

export default {

   data(){
      return {
         design: d.design,
         
         imgs: d.design,
         index: 0,
         visible: false
      }

   },
   methods: {
      handleExpand(i){

   

         this.index=i
         this.visible = true
         //alert(this.$parent.visible)
         
      },
      handleHide(){
         this.visible = false
      }
   },


}
</script>

<style>

#dd2{

   grid-row: 1/3;
   grid-column: 2;

}

#dd3{
   grid-row: 1/3;
   grid-column: 3/5;
}

#dd5{
   grid-column: 1/4;
}

#dd72{
   grid-row: 2;
   grid-column: 3/4 !important;
}




</style>
