<template>
   <!-- v-layout row wrap justify-center align-center >
      <v-flex class='ds-img' xs3 v-for='i in quality' style='padding: 1px' >
         <v-img cover :src='i' aspect-ratio='1.5' max-height=260 > </v-img>
      </v-flex>
   </v-layout> -->

   
   <div style='height: calc(100vh - 48px); position: relative; z-index: 1000 !important;'>

      <vue-easy-lightbox
         moveDisabled
         :visible="visible"
         :imgs="imgs"
         :index='index'
         @hide='handleHide'
         style='z-index: 10000000 !important;position: fixed; top: 200vh; height: 100vh; width: 100vw'
               
      ></vue-easy-lightbox>

      <div class='gridWrapper' style='height: 100%;  ' >
         <div v-for='(i,k) in lab' :id="'lt'+(k+1)" style='background: white;' 
            class='innerGrid'
            
         >

            <template v-if='k==5'>
               <v-img ::lzy-src='i'
                  :src='i+"?lazy"'
               
                  :contain='k==5'
                  :cover='k!=5' 
                  
                  :aspect-ratio='k==5 ? 0.9 : 0'
                  position='center center'
                  @click='handleExpand(k)'
                           > 
               </v-img> 
            </template>

            <template v-else>
               <img :src='i'
               
                  class='gridImgStretch'
                  @click='handleExpand(k)'
                           > 
               </img> 
            </template>

            
         </div>
      </div>
   </div>


</template>

<script>

import d from '../../assets/about.js' 

export default {

   data(){
      return {
         lab: d.lab,
         
         imgs: d.lab,
         index: 0,
         visible: false
      }
   },
   methods: {
      handleExpand(i){

   

         this.index=i
         this.visible = true
         //alert(this.$parent.visible)
         
      },
      handleHide(){
         this.visible = false
      }
   }

}
</script>

<style>

   #lt5{
      grid-row: 2/4;
      grid-column: 1;
      background: rgba(255,0,0,0.4);
   }

   #lt6{
            grid-row: 2/4;
            grid-column: 2;
            background: rgba(255,0,0,0.4);
   }

   #lt7{
      grid-row: 2/4;
      grid-column: 3/5;
   }

</style>
