<template>
   <!-- v-layout row wrap justify-center align-center >
      <v-flex class='ds-img' xs3 v-for='i in quality' style='padding: 1px' >
         <v-img cover :src='i' aspect-ratio='1.5' max-height=260 > </v-img>
      </v-flex>
   </v-layout> -->

   <div style='height: calc(100vh - 48px); position: relative; z-index: 1000 !important;'>
      <vue-easy-lightbox
         moveDisabled
         :visible="visible"
         :imgs="imgs"
         :index='index'
         @hide='handleHide'
         style='z-index: 10000000 !important;position: fixed; top: 100vh; height: 100vh; width: 100vw'
               
         ></vue-easy-lightbox>
      <div class='gridWrapper' style='height: 100%;  ' >
         <div v-for='(i,k) in quality' :id="'qa'+(k+1)" style='background: white;' 
            class='innerGrid'
            
         >

            <template v-if='k==3'>
               <v-img ::lzy-src='i' :cover='k!=4' :contain='k==4'
                  :src='i+"?lazy"'
                  position='center center'
                  :max-height='k==2 ? 836 : "100%"' 
                  @click='handleExpand(k)'
                        
               >

               </v-img>
            </template>

            <template v-else>
            </template>
            <img :src='i'
               class='gridImgStretch'
               position='center center'
               :max-height='k==2 ? 836 : "100%"' 
               @click='handleExpand(k)'
                        
            >

            </img>

            
         </div>
      </div>
   </div>
</template>

<script>

import d from '../../assets/about.js' 

export default {

   data(){
      return {
         quality: d.quality,
         
         imgs: d.quality,
         index: 0,
         visible: false
      }
   },
   methods: {
      handleExpand(i){

   

         this.index=i
         this.visible = true
         //alert(this.$parent.visible)
         
      },
      handleHide(){
         this.visible = false
      }
   }

}
</script>

<style>


   #qa5{
         grid-row: 2;
         grid-column: 1/3;
         background: rgba(255,0,0,0.4);
   }

   #qa3{
      grid-row: 1/3;
      grid-column: 3/5;
   }

</style>
